.TodoItem {
  background-color: #15101c;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  width: 100%;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
  border-radius: 20px;
}

.TodoItem-p {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
  color: #9e78cf;
}
.TodoItem-p--completed {
  text-decoration: line-through;
  color: #78cfb0;
}
